import React, { useState, useEffect } from 'react' 
import Logo from '../assets/logo.svg'
import EthSelect from '../assets/eth-select.svg'
import NFTYLabel from '../assets/nfty-label.svg'
import MetaMaskGray from '../assets/metamask-gray.svg'
import { default as MetaMaskColor } from '../assets/metamask-color.svg'
import LoginDivider from '../assets/login-divider.svg'
import WalletConnectColor from '../assets/wallet-connect-color.svg'
import WalletConnectGray from '../assets/wallet-connect-gray.svg'
import { default as EthOption1 } from '../assets/eth-option-1.svg';
import Wallet from '@project-serum/sol-wallet-adapter';
import moment from "moment"
import SolanaColor from '../assets/solana-color.svg'
import SolanaGray from '../assets/solana-gray.svg'
import BonafideColor from '../assets/bonafide-color.svg'
import BonafideGray from '../assets/bonafide-gray.svg'
import SolSelect from '../assets/sol-select-1.svg'
import { ethers } from "ethers";
import axios from 'axios';
import ClimbingBoxLoader from "react-spinners/ClipLoader";
import SolanaOption1 from '../assets/solana-option-1.svg'

const Login = (props) => {

    /*const {
        
    } = props;*/

    // const connection = useMemo(() => new Connection(network), [network]);
    const[metamaskColor, setMetamaskColor] = useState(false);
    const[solanaColor, setSolanaColor] = useState(false);
    const[bonafideColor, setBonafideColor] = useState(false);
    const[walletConnectColor, setWalletConnectColor] = useState(false);
    const[loading, setLoading] = useState(false);
    const[rawMessage, setRawMessage] = useState("");   
    const[message, setMessage] = useState("");
    const[isOpen, setIsOpen] = useState(false); 
    const[ethOptions, showEthOptions] = useState(true);
    const[solanaOptions, showSolanaOptions] = useState(false);
    const[is404, setIs404] = useState(false)

    useEffect(() => {
        //console.log(window.location.href.split("/")[5])
        //console.log(window.location.href.split("/"))
        async function doFetch(){
            let id = window.location.href.split("/")[5]                        
            let hash = window.location.href.split("/")[4]   
            
            if(hash != "#" || !id){
                setIs404(true)
                return
            }

            let endpoint=`https://x10298-things-and-stuff-12983.herokuapp.com/connect/${id}`  
            await axios.get(endpoint)
                        .then((response) => {
                            console.log("Response", response)
                            setRawMessage(response.data)
                            let rawMessage = response.data       
                            if(rawMessage){
                                if(rawMessage.code === 403){
                                    setIs404(true)
                                    return
                                }                                
                            }
                            let message = `NFTY Labs signed message: v${rawMessage.Version}
                                           You are authenticating to: ${rawMessage.Service}
                                           Authorization ID: ${rawMessage.ID} 
                                           Void after: ${moment(rawMessage.Until).format('MMMM Do YYYY, h:mm a')}`
                            setMessage(message);
                        })
                        .catch(err => console.error("Error occurred", err))
        }
        doFetch();
    }, []);

    useEffect(() => {

    }, [isOpen])

    const chooseSolOpt = () => {
        showEthOptions(false)
        showSolanaOptions(true)
    }

    const chooseEthOpt = () => {
        showEthOptions(true)
        showSolanaOptions(false)
    }

    const connectSolanaWalletAndSign = async () => {
        try{
            //let connection = new Connection(clusterApiUrl('devnet'));
            let providerUrl = 'https://www.sollet.io';
            let wallet = new Wallet(providerUrl);
            wallet.on('connect', publicKey => console.log('Connected to ' + publicKey.toBase58()));
            wallet.on('disconnect', () => console.log('Disconnected'));
            await wallet.connect();

            //let transaction = SystemProgram.transfer({
            //fromPubkey: wallet.publicKey,
            //toPubkey: wallet.publicKey,
            //lamports: 100,
            //});
            //let { blockhash } = await connection.getRecentBlockhash();
            //transaction.recentBlockhash = blockhash;
            //let signed = await wallet.signTransaction(transaction);
            //let txid = await connection.sendRawTransaction(signed.serialize());
            //await connection.confirmTransaction(txid);
            let signed = await wallet.sign(message)
            console.log(signed)
            alert("Service currently unavailable")
        }catch(e){
            console.log("Solana connect error");
            alert("Unable to connect to solana wallet. Please try again later")
       }
    }

    //https://docs.walletconnect.org/quick-start/dapps/web3-provider
    /*const connectWalletConnectAndSign = async  () => {        
        try{
             //  Create WalletConnect Provider
            const provider = new WalletConnectProvider({
                infuraId: "27e484dcd9e3efcfd25a83a78777cdf1",
            });            
            //  Enable session (triggers QR Code modal)
             await provider.enable();
             //  Wrap with Web3Provider from ethers.js
            const web3Provider = new providers.Web3Provider(provider);
            const signedMessage = await web3Provider.eth.sign(message);
             //const signer = web3Provider.getSigner()
             //const signedMessage = await signer.signMessage(JSON.stringify(rawMessage))
             console.log("signed rawMessage", signedMessage)
 
             //verify the signature
             await axios.post("https://x10298-things-and-stuff-12983.herokuapp.com/verify", {
                 //name: rawMessage.Service,
                 id: rawMessage.ID,
                 //timestamp: rawMessage.Until,
                 signature: signedMessage,
                 //address: account
             }).then((response) => {
                 console.log("Verified, data is", response.data)
                 alert("Service currently unavailable")
                 
             }).catch(err => {
                 console.error("Error", err)
                 alert( "Not authorized, did you pick the wrong account?")
             });            
        }catch(e){
             console.log("Metamask connect error");
             alert("Unable to connect to metamask wallet. Please try again later")
        }
     }*/

    const connectMetamaskWalletAndSign = async  () => {        
       if(!rawMessage){
           alert("Message is invalid. Please try again later")
           return
       }
       try{           
            await window.ethereum.enable()
            let account;
            const provider = await new ethers.providers.Web3Provider(window.ethereum)            
            account = await provider.listAccounts().then(result => {
            let accountFound = result[0];
            return accountFound
            });
            const signer = provider.getSigner()
            const signedMessage = await signer.signMessage(message)
            console.log("signed rawMessage", signedMessage)

            setLoading(true)

            //verify the signature
            await axios.post("https://x10298-things-and-stuff-12983.herokuapp.com/verify", {
                //name: rawMessage.Service,
                id: rawMessage.ID,
                //timestamp: rawMessage.Until,
                signature: signedMessage,
                address: account
            }).then((response) => {
                console.log("Verified, data is", response.data)
                if(response.data.payload){
                    window.location.href = response.data.payload
                }
                else{
                    alert("Service currently unavailable")
                }                            
            }).catch(err => {
                console.error("Error", err)
                alert( "Not authorized, did you pick the wrong account?")
            });            
       }catch(e){
            console.log("Metamask connect error");
            alert("Unable to connect to metamask wallet. Please try again later")
       }
       setLoading(false)
    }

    if(is404){
        return(
            <div className="flex justify-center items-center my-24">
                <p>Not found</p>
            </div>
        )                
    }


    if(loading){
        return(
            <div className="flex justify-center items-center my-24">
                <ClimbingBoxLoader loading={loading} size={150} />
            </div>
        )
    }

    return (
        <div className="flex justify-center items-center animate-fade-in-down">


            <div className="flex shadow-2xl rounded-2xl flex-col md:h-1/2 md:w-4/12 md:my-12 md:mb-12  lg:h-1/2 lg:w-4/12 lg:my-12 lg:mb-12 justify-center items-center">        
                   
                    <img alt="" className="my-2 h-20 mb-4" src={Logo}/> 
                    
                   
                    
                    <div className="my-2 cursor-pointer">
                        <div className="flex flex-col justify-center items-center">
                        <div className="rounded-2xl shadow-2xl bg-white flex flex-col">
                                {
                                    ethOptions
                                    ?
                                        <div className="flex justify-center items-center rounded-4xl shadow-4xl">
                                            <img alt="" className="w-5/6 my-4 hover:rounded-3xl hover:border-2 hover:border-red-200" src={EthSelect} onClick={() => setIsOpen(!isOpen)} />
                                        </div>
                                    :
                                    solanaOptions
                                    ?
                                        <div className="flex justify-center items-center rounded-4xl shadow-4xl ">
                                            <img alt="" className="w-5/6 my-4 animate-fade-in-down" src={SolSelect} onClick={() => setIsOpen(!isOpen)} />
                                        </div>
                                    :
                                    null                        
                                }
                                {
                                    isOpen
                                    ?
                                    <div>
                                        <div className={
                                            ethOptions ? "animate-fade-in-down flex flex-col items-center justify-center rounded-2xl border-2 border-black"
                                            : "flex flex-col items-center justify-center animate-fade-in-down"
                                        }>                                         
                                            <div className="flex items-center justify-center w-5/6 ">
                                                <img alt="" className="my-4 " src={EthOption1} onClick={() => {
                                                    chooseEthOpt()
                                                }}/>
                                            </div>                                        
                                        </div>

                                        <div className={
                                            solanaOptions ? "animate-fade-in-down flex flex-col items-center justify-center rounded-2xl border-2 border-black"
                                            : "flex flex-col items-center justify-center animate-fade-in-down"
                                        }>                                         
                                            <div className="flex items-center justify-center w-5/6 ">
                                            <img alt="" className="w-5/6 my-4" src={SolanaOption1} onClick={() => {
                                                chooseSolOpt()
                                                }}/>
                                            </div>                                        
                                        </div>                                    
                                    </div>
                                    :
                                    null
                                }
                            </div>
                            <div>                
                            </div>             
                        </div>
                    </div>     



                    <img alt="" className="h-4 mt-4 mb-8" src={NFTYLabel}/>
                    
                   {
                       ethOptions
                       ?
                       <div className="flex items-center justify-center flex-col">
                            <div className="h-20 mb-12">
                                    <img alt="" 
                                    className={metamaskColor? "animate-fade-in-logo cursor-pointer":"cursor-pointer"}

                                    onMouseEnter={() => setMetamaskColor(true)}
                                    onMouseLeave={() => setMetamaskColor(false)}
                                    src={ metamaskColor ? MetaMaskColor : MetaMaskGray}
                                    onClick={()=>connectMetamaskWalletAndSign()}
                                    />
                             </div>
                                
                                
                             <div className="my-8 border border-gray-500 cursor-pointer w-1/2 flex justify-center items-center">
                                <p className="label-text text-gray-500 font-bold"
                                onClick={()=>connectMetamaskWalletAndSign()}>Metamask</p>
                            </div>                                      
                            
                                                            
                            <img alt="" className="mt-4 mb-8"  src={LoginDivider} />
                            
                            <div className="h-20 lg:mb-4">
                                <img alt="" 
                                className="cursor-pointer"
                                onMouseEnter={() => setWalletConnectColor(true)}
                                onMouseLeave={() => setWalletConnectColor(false)}
                                src={ walletConnectColor ? WalletConnectColor : WalletConnectGray}
                                />
                            </div>
                            
                            <div className="my-9 border border-gray-500 cursor-pointer w-1/2 flex justify-center items-center">
                                <p className="label-text text-gray-500 font-bold">WalletConnect</p>
                            </div>               
                       </div>
                       :
                       solanaOptions
                       ?
                        <div className="flex items-center justify-center flex-col">
                            <div className="h-20 mb-12">
                                    <img alt="" 
                                    className="cursor-pointer"
                                    onMouseEnter={() => setSolanaColor(true)}
                                    onMouseLeave={() => setSolanaColor(false)}
                                    src={ solanaColor ? SolanaColor : SolanaGray}
                                    onClick={()=>connectSolanaWalletAndSign()}
                                    />
                                </div>
                                
                                
                                <div className="my-8 border border-gray-500 cursor-pointer w-1/2 flex justify-center items-center">
                                <p className="label-text text-gray-500 font-bold">Solana</p>
                            </div>                                   
                            
                                                            
                            <img alt="" className="my-8"  src={LoginDivider} />
                            
                            <div className="h-20 lg:mb-4">
                                <img alt="" 
                                className="cursor-pointer"
                                onMouseEnter={() => setBonafideColor(true)}
                                onMouseLeave={() => setBonafideColor(false)}
                                src={ bonafideColor ? BonafideColor : BonafideGray}
                                />
                            </div>
                            
                            <div className="my-12 border border-gray-500 cursor-pointer w-1/2 flex justify-center items-center">
                                <p className="label-text text-gray-500 font-bold">Bonfida</p>
                            </div>                            
                        </div>
                       :
                       null
                   }

            </div>

        </div>
    );
}

export default Login;



